<template>
  <div>
    <div style="padding: 0 20px">
      <el-button size="mini" style="margin-right: 10px" @click="scaleAction">{{show?'收起':'展开'}}</el-button>
      <el-popover
          placement="bottom"
          width="200"
          trigger="click">
        <div>
          <div style="padding: 4px 0" v-for="(item, idx) in Object.keys(value.properties)" :key="idx">{{ item }}</div>
          <div class="flex-row">
            <el-input size="mini" v-model="model"></el-input>
            <el-button size="mini" @click="addProperties">添加</el-button>
          </div>
        </div>
        <el-button size="mini" slot="reference">Properties</el-button>
      </el-popover>
    </div>
    <div v-for="(item, idx) in Object.keys(value.properties)" :key="idx" v-show="show">
      <table-column v-model="value.properties[item]" @delete="deleteAction"></table-column>
    </div>
  </div>
</template>

<script>

import {getDefaultValue} from "@/common/jsonType";

export default {
  name: "tableObject",
  props: {
    value: {}
  },
  data() {
    return {
      model: '',
      show: true
    }
  },
  methods: {
    async addProperties() {
      const properties = Object.keys(this.value.properties)
      if (properties.indexOf(this.model) !== -1) {
        this.$message.error('已存在该属性')
        return;
      }
      await this.$nextTick()
      this.value.properties[this.model] = {
        type: 'string',
        key: this.model,
        properties: {},
        value: getDefaultValue('string')
      }
      this.model = ''
    },
    async deleteAction({ key }){
      await this.$nextTick()
      delete this.value.properties[key]
      this.$forceUpdate()
    },
    scaleAction(){
      this.show = !this.show
    }
  }
}
</script>

