const menu = [{
    path: '/',
    component: 'default',
    redirect: '/home',
    always: true,
    children: [{
        path: 'home',
        label: '首页',
        component: 'home/home'
    }]
}, {
    path: '/sign',
    component: 'default',
    label: '加密',
    redirect: '/sign/xcode',
    children: [{
        path: 'xcode',
        label: '自定签名',
        component: 'sign/sign'
    }]
}, {
    path: '/view',
    component: 'default',
    label: '页面',
    redirect: '/view/create',
    children: [{
        path: 'create',
        label: 'Vue Table',
        component: 'vue/table'
    }]
}]

export default menu;