import Vue from 'vue';
import VueRouter from 'vue-router';
import store from "@/store";
import Layout from "@/components/layout/layout";
import sign from './sign';

Vue.use(VueRouter);

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}
function requireComponent(file) {
    return () => import(`@/views/${file}.vue`);
}

function defaultComponent(list) {
    const routes = [];
    if (typeof list === 'string') {
        list = JSON.parse(list);
    }
    list.forEach(item => {
        const map = {};
        if (item.component === 'default') {
            map.component = Layout;
        } else {
            try {
                map.component = requireComponent(item.component);
            } catch (e) {
                map.component = requireComponent('error/error');
            }
        }
        if (item.children !== undefined && item.children.length > 0) {
            map.children = defaultComponent(item.children);
        }
        if (item.redirect !== undefined) {
            map.redirect = item.redirect
        }
        item.always !== undefined ? map.always = item.always : '';
        if (item.path !== undefined) {
            map.path = item.path;
        }
        map.meta = {
            title: item.label,
            icon: item.icon
        }
        map.label = item.label;
        map.name = item.name;
        routes.push(map);
    });
    return routes;
}


const routes = defaultComponent(sign);
console.log(routes)

store.dispatch('setMenu', [{
    label: '工具',
    list: sign
}]).then(() => {
})
store.dispatch('setRoutes', routes).then(() => {
})


export default new VueRouter({
    routes
})