export const jsonType = [
    'string',
    'array',
    'object',
    'boolean',
    'null'
]

export const getDefaultValue = type => {
    switch (type){
        case 'string':
            return '';
        case 'array':
            return [];
        case 'object':
            return {};
        case 'boolean':
            return false;
        case 'null':
            return null
    }
}