<template>
  <div style="border: 1px solid #ddd;margin: 8px;border-radius: 5px;padding: 10px 0;">
    <el-row>
      <el-col :span="5">
        <span style="padding: 0 20px;font-size: 18px">{{ value.key || '' }}</span>
      </el-col>
      <el-col :span="5">
        <el-select :disabled="disable" size="mini" v-model="value.type" @change="changeValue">
          <el-option v-for="(item, idx) in jsonType" :key="idx" :label="item" :value="item"></el-option>
        </el-select>
      </el-col>
      <el-col :span="5" v-if="deleteShow">
        <el-button size="mini" type="danger" @click="deleteProperties">删除</el-button>
      </el-col>
    </el-row>
    <div style="padding: 10px 0">
      <table-array v-model="value" v-if="value.type==='array'"></table-array>
      <table-object v-model="value" v-if="value.type==='object'"></table-object>
      <table-string v-model="value" v-if="value.type==='string'"></table-string>
    </div>
  </div>
</template>

<script>
import {jsonType, getDefaultValue} from "@/common/jsonType";

export default {
  name: "tableColumn",
  props: {
    value: {},
    deleteShow: {
      default: true
    },
    disable: {
      default: false
    }
  },
  data() {
    return {
    }
  },
  watch: {

  },
  computed: {
    jsonType() {
      return jsonType;
    }
  },
  methods: {
    changeValue(val){
      this.value.value = getDefaultValue(val);
      this.$forceUpdate()
    },
    deleteProperties(){
      this.$emit('delete', this.value);
    }
  }
}
</script>

