import Vuex from 'vuex';
import Vue from 'vue';

Vue.use(Vuex);


const store = new Vuex.Store({
    state: {
        routes: [],
        menu: []
    },
    actions: {
        setRoutes({state}, routes) {
            state.routes = routes;
        },
        setMenu({state}, menu) {
            state.menu = menu;
        }
    }
})


export default store;