<template>
  <div style="padding: 0 20px">
    <div>
      <el-button size="mini" style="margin-right: 10px" @click="scaleAction">{{ show ? '收起' : '展开' }}</el-button>
      <el-button size="mini" @click="addProperties">添加</el-button>
    </div>
    <div v-for="(item, idx) in Object.keys(value.properties)" :key="idx" v-show="show">
      <table-column v-model="value.properties[item]" @delete="deleteAction"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "tableArray",
  props: {
    value: {}
  },
  data() {
    return {
      show: true,
      indexT: 0
    }
  },
  methods: {
    async addProperties() {
      if (this.value.properties === undefined) {
        this.value.properties = {};
      }
      ++this.indexT
      const index = this.indexT;
      console.log(index);
      await this.$nextTick()
      this.value.properties[`item${index}`] = {
        type: 'string',
        value: '',
        properties: {},
        key: `item${index}`
      }
      this.$forceUpdate()
    },
    scaleAction() {
      this.show = !this.show
    },
    async deleteAction({key}) {
      await this.$nextTick()
      delete this.value.properties[key]
      this.$forceUpdate()
    },
  }
}
</script>

