<template>
  <div class="flex-row flex-1">
    <div class="flex-column flex-just-center flex-align-end" style="width: 60px;background-color: #f2f2f2">
        <div class="flex-row flex-just-center flex-align-center" style="width: 100%" v-for="(item, idx) in menus" :key="idx">
            <span>{{item.label}}</span>
        </div>
    </div>
    <div style="width: 200px" class="flex-column">
      <el-scrollbar wrap-class="el-scrollbar__wrap" class="flex-1 h-0" style="background-color: #304156">
        <el-menu
            :active-text-color="activeTextColor"
            :text-color="textColor"
            :background-color="backgroundColor"
            :collapse="collapse"
        >
          <side-bar v-for="(item, index) in pages" :key="index" v-bind="$attrs" path="" :item="item"></side-bar>
        </el-menu>
      </el-scrollbar>
    </div>
    <div class="flex-1 w-0 flex-column">
      <div></div>
      <div class="v-padding-20 flex-1 h-0 flex-column" style="background-color: #f2f2f2">
        <router-view/>
      </div>
    </div>
  </div>
</template>

<script>
import sideBar from "@/components/layout/sideBar";

export default {
  name: "layout",
  data() {
    return {
      activeTextColor: '#409eff',
      textColor: '#bfcbd9',
      backgroundColor: '#304156'
    }
  },
  components: {
    sideBar: sideBar
  },
  computed: {
    pages() {
      return this.$store.state.routes
    },
    collapse() {
      return this.$store.state.collapse
    },
    menus () {
      return this.$store.state.menu;
    }
  },
  mounted() {
    // console.log(this.pages)
  },
}
</script>

