<template>
  <div v-if="item.hidden === undefined || item.hidden===false">
    <template v-if="oneChild">
      <div @click="linkTo(sideItem.path)">
        <el-menu-item :index="getSidePath(sideItem.path)">
          <span slot="title">{{ sideItem.label }}</span>
        </el-menu-item>
      </div>
    </template>
    <el-submenu ref="subMenu" v-else :index="getSidePath(sideItem.path)" popper-append-to-body backgroundColor="#222222"
                :class="{'el-collapse-transition-span': collapse}"
                style="background-color: #1a1a1f">
      <template slot="title">
        <span slot="title">{{ sideItem.label }}</span>
      </template>
      <side-bar v-for="(sit, idx) in sideItem.children" :key="idx" :item="sit" :path="getSidePath(sideItem.path)"
                :nest="true"></side-bar>
    </el-submenu>
  </div>
</template>

<script>
export default {
  name: "sideBar",
  props: {
    item: {},
    path: {
      default: ''
    }
  },
  computed: {
    oneChild() {
      return !(this.sideItem !== undefined && this.sideItem.children !== undefined && this.sideItem.children !== null && this.sideItem.children.length > 0);
    },
    collapse() {
      return this.$store.state.collapse
    }
  },
  data() {
    return {
      sideItem: {}
    }
  },
  watch: {
    item: {
      handler(val) {
        this.sideItem = Object.assign({}, val);
        this.reloadSieItem();
      },
      immediate: true
    }
  },
  methods: {
    getSidePath(val) {
      if (this.path.lastIndexOf('/') === this.path.length - 1) {
        return this.path + val;
      }
      return this.path + '/' + val;
    },
    linkTo(val) {
      // console.log(val);
      const path = this.getSidePath(val);
      this.$router.push(path)
    },
    getRelativePath(p, c) {
      if (p.lastIndexOf('/') === p.length - 1) {
        return p + c;
      }
      return p + '/' + c;
    },
    reloadSieItem() {
      const result = !(this.item !== undefined && this.item.children !== undefined && this.item.children !== null && this.item.children.length > 0);
      if (result === false && this.item.children.length === 1 && this.item.always) {
        const it = this.item.children[0];
        this.sideItem.path = this.getRelativePath(this.item.path, it.path);
        this.sideItem.meta = it.meta;
        this.sideItem.name = it.name;
        this.sideItem.label = it.label;
        this.sideItem.children = [];
      }
    },
  }
}
</script>

